.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#home{
  display: block;
  float: left;
}

#header{
  display: block;
  padding: 30px;
}

li a{
  color: rgb(76, 167, 220);
  text-decoration: none;
}


nav a{
  color: rgb(0, 0, 0);
  text-decoration: none;
}


.libimg{
  width: 100%;
  height: 500px;
 
}


.maindiv{
  min-height: 700px;
}


.imgleft{
  display: block;
  position:absolute;
  top: 50px;
  left: 50%;
}


.imgright {
display: block;
position: absolute;
top: 50px;
right: 50%;
}


h1 {
  color: white;
}


.imgcontainer {
  position: relative;
  text-align: center;
}


button{
  background-color: rgb(76, 167, 220);
  color: white;
  margin-right: 5px; 
}

.formdiv{
  float:center;
  margin: auto auto auto auto;
  max-width: 60%;
}

.signuplogo{
  -ms-flex-align: center;
      align-items: center;
}

.form-control{
  background-color: rgb(164, 206, 245)
}

#bookheader{
  float: left;
}

.homelogo{
 display: inline;
}


